.sidebar{
    display: flex;
    /* justify-content: center;
     */
     align-items: center;
    flex-direction: column;
    width: 60px;
    height: 100vh;
    position: sticky;
    left: 0;
    background-color: #1b1c3c;
    z-index: 100;
}
.sidebar>.sideIcon{
    cursor: pointer;
    margin-top: 10px;
    margin-bottom: 10px;
    color: #f0ebeb;
    font-size: 2rem;
}
.sidebar>.sideIcon:hover{
    transform: scale(1.2,1.2);
}
