.middlebar{
    width: 60vw;
    height:100vh;
    /* padding: 10px; */
    /* padding-bottom: 0px; */
    background-color: #30333c;
    display: flex;
    flex-direction: column;
}
.middlebar>.midhead{
    margin-top: 30px;
    text-align: center;
    font-size: 60px;
    font-weight: 600;
    color: rgb(161, 234, 66);
    font-weight: 600;
    text-decoration: underline;
    margin-bottom: 18px;

}
.btn_choose{
    background-color: beige;
    border-radius: 30px;
    align-self: center;
    display: flex;
}
.btn_choose>label:nth-child(1)
{
    background-color: rgb(10, 159, 110);
}
.btn_choose label{
    cursor: pointer;
    text-align: center;
    width: 50px;
    /* background-color: #e32121; */
    border-radius: 30px;
    padding: 10px;
    
}
.btn_choose label:focus{
    /* color: white; */
    /* background-color: #e51111; */
}
.hide{
    display: none;
}
.mid_container{
    padding: 20px;
}
.mid_container>.Albums{
    display: flex;
}
.mid_container>div{
    /* width: 100%; */
    flex-direction: column;
    align-items: center;
}
.mid_container>div img{
    /* position: absolute; */
    max-width: 100px;
    height: 100px;
}
.mid_container>div>p{
    margin-bottom: 20px;
    /* text-align: center; */
    font-size: x-large;
    color: aliceblue;
    text-decoration: underline;
}
.mid_container>div>div{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
}
.mid_container>div>div>div{
    margin-top: 10px;
position: relative;
    margin-right: 20px;
    display: flex;
    flex-direction: column;
}
.mid_container>div>div>div>label{
    font-weight: 600;
    color: beige;
    margin-top: 10px;
    font-size: small;
}
.mid_container>div>div>div>.mid_icon{
    left: 5px;
    cursor: pointer;
    color: aqua;
    background-color: #30333c;
    border-radius: 50%;
    position: absolute;
    bottom: 40px;
}
@media only screen and (max-width:700px) {
    .middlebar>.midhead{
        font-size: 40px;
    }
    .mid_container>div>div{
        grid-template-columns: 1fr 1fr 1fr;
    }
}