.bottombar{
    background-color:#282828;
    width: 100%;
    height: 80px;
    position: fixed;
    bottom: 0;
    display: flex;
    /* left: 59px; */
    z-index: 500;
}
.bottombar>.song_detail{
    padding: 8px;
    display: flex;
    flex:0.3;
    /* border: 2px solid #fff; */
}
.bottombar>.player{
    display: flex;
    flex-direction: column;
    flex: 0.4;
    /* border: 2px solid #fff; */
}
.bottombar>.player>.player_icon{
    padding: 7px;
    color: #cfc7c7;
    display: flex;
    align-self: center;
}
.bottombar>.extra_detail{
    font-size: 30px;
    flex: 0.3;
    display: flex;
    justify-content: end;
    align-items: center;
    /* border: 2px solid #fff; */
}
.play-pause{
    margin-right: 8px;
}
.icon_pl{
    cursor: pointer;
    margin-right: 8px;
}
.bottombar>.player>.player_stats{
    display: flex;
    align-items: center;
    padding: 8px;

}
.bottombar>.player>.player_stats>label{
    color: white;
}
.bottombar>.player>.player_stats>div{
    width: 100%;
    height: 4px;
    background-color: #fff;
    margin-left: 7px;
    margin-right: 7px;
    border-radius: 30px;
    background-size: 30% auto;
}
.pl_stat{
    cursor: pointer;
    margin: 0;
    padding: 0;
}
.pl_stat>div{
    
    height: 100%;
    border-radius: 30px;
    background-color: #1bce92;
}
.song_detail>img{
    margin-right: 20px;
    max-width: 60px;
    height: 60px;
}
#myAudio{
    width: 1px;
    height: 1px;
}
.song_detail>div{
    display: flex;
    flex-direction: column;
}
.song_detail>div>.song_name{
color: #cfc7c7;
 font-size: larger;
 font-weight: 600;
}
.song_detail>div>.artist_name{
    color:#fff;
    font-size: smaller;
    font-weight: 600;
}
.hide{
    display: none;
}
@media only screen and (max-width:700px) {
    .song_detail>img{
        margin-right: 20px;
        max-width: 60px;
        height: 60px;
    }
    .song_detail>div>.song_name{
       
         font-size: x-small;
         font-weight: 600;
        }
        .song_detail>div>.artist_name{
            font-size: xx-small;
            font-weight: 200;
        }
}