.rightbar{
    width: 38vw;
    height: 100vh;
    background-color: #1d2332;
    display: flex;
    flex-direction: column;
}
.song_details{
    display: flex;
    flex-direction: column;
    align-items: center;
    align-self: center;
    margin-top: 40px;
}
.song_details>p{
    color: #f1ecec;
    font-size: larger;

}
.some_recent>p{
    color: #c1bcf1;
    font-size: x-large;
    text-decoration: underline;
    margin-bottom: 20px;
}
.song_details>img{
    margin-top: 10px;
    margin-bottom: 10px;
    align-self: center;
    max-width: 10em;
    height: auto;
}
.some_recent{
    color: #ffffff;
    margin-top: 50px;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.some_recent label{
    color: #e1faf7;
}
.some_recent>div{
    margin-bottom: 10px;
    align-items: center;
    
    display: flex;
}
.some_recent>div>div{
    
    font-size: larger;
    margin-left: 10px;
}
.some_recent .icon{
    
    display: flex;
    align-items: center;
}
.hide{
    display: none;
}
.icon_btn{
    border-radius: 50%;
    background-color: #047a36;
    cursor: pointer;
}




@media only screen and (max-width:700px) {
    .rightbar{
        display: none;
    }
    .middlebar{
        width: 100%;
    }
}